<template>
  <div>
    <Loader v-show="!loaded" />
    <!--<div class="learn_notification">
      Choose Period
      <select @change="createGeo" v-model="history">
        <option value="" selected disabled hidden>Period</option>
        <option value="2000bc">2000 BC</option>
        <option value="1000bc">1000 BC</option>
        <option value="500bc">500 BC</option>
        <option value="200bc">200 BC</option>
        <option value="1bc">1 BC</option>
        <option value="400">400</option>
        <option value="600">600</option>
        <option value="800">800</option>
        <option value="1000">1000</option>
        <option value="1279">1279</option>
        <option value="1492">1492</option>
        <option value="1530">1530</option>
        <option value="1650">1650</option>
        <option value="1715">1715</option>
        <option value="1783">1783</option>
        <option value="1815">1815</option>
        <option value="1880">1880</option>
        <option value="1914">1914</option>
        <option value="1920">1920</option>
        <option value="1938">1938</option>
        <option value="1945">1945</option>
        <option value="1994">1994</option>
      </select>
    </div>-->
    <div id="map" v-if="loaded">    
      <v-map ref="map" :minZoom="2" :options="options.map" class="fullHeight">
        <v-geojson :geojson="geojson" :options="options.geojson"></v-geojson>
      </v-map>    
    </div>
    <div class="slider">
      <vue-slider ref="slider" v-bind="options.slider" v-model="history" @callback="createGeo"></vue-slider>
      <img :src="`../img/${playPressed ? 'stop' : 'play'}.svg`" width="32" height="32" @click="playHistory" class="btn-history"/>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
import Vue2Leaflet from 'vue2-leaflet'
import Loader from './Loader'
import vueSlider from 'vue-slider-component'

export default {
  data () {
    return {
      world: [],
      geojson: [],
      options: {
        map: {
          zoom: 2,
          center: [0, 0],
          maxBounds: [[90, -180], [-60, 225]]
        },
        geojson: {
          stroke: true,
          fill: true,
          fillColor: 'rgba(255,255,255,0.5)',
          color: '#000',
          fillOpacity: 1,
          onEachFeature: this.onEachFeature
        },
        slider: {
          width: "80%",
          tooltip: "always",
          lazy: true,
          disabled: false,
          piecewise: true,
          piecewiseLabel: true,
          style: {
            "margin-left": "10%"
          },
          data: [
            "2000 BC",
            "1000 BC",
            "500 BC",
            "200 BC",
            "1 BC",
            "400",
            "600",
            "800",
            "1000",
            "1279",
            "1492",
            "1530",
            "1650",
            "1715",
            "1783",
            "1815",
            "1880",
            "1914",
            "1920",
            "1938",
            "1945",
            "1994"
          ],
          piecewiseStyle: {
            "backgroundColor": "#fff",
            "visibility": "visible",
            "width": "12px",
            "height": "12px"
          },
          piecewiseActiveStyle: {
            "backgroundColor": "#3498db"
          },
          labelActiveStyle: {
            "color": "#3498db"
          },
          bgStyle: {
            "backgroundColor": "#fff",
            "boxShadow": "inset 0 0 3px rgba(0,0,0,.36)"
          }
        }
      },
      loaded: false,
      history: '2000 BC',
      playPressed: false,
      interval: undefined
    }
  },
  created () {
    this.createMap()    
  },
  methods: {
    createMap () {
      this.$http.get(`../json/map.json`)
        .then((response) => {
          this.world = [...response.body.features]
          this.createGeo()
        }, response => {
          console.log(response)
      })
    },
    createGeo () {
      let period = this.history.replace(' ', '').toLowerCase()
      this.$http.get(`../json/history/${period}.json`)
      .then(response => { 
        this.geojson = [this.world[0], ...response.body.features]
        this.loaded = true
      }, response => {
        console.log(response)
      })
    },
    onEachFeature (feature, layer) {
      if (layer.feature.properties.name === 'world') {
        layer.setStyle({ stroke: false })
        return
      }
      layer.setStyle({ fillColor: this.randomColor() })
      layer.bindPopup(layer.feature.properties.name)
    },
    randomColor () {
      return `rgb(${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)})`
    },
    playHistory () {
      if (!this.playPressed) {
        this.playPressed = true
        this.$refs.slider.setIndex(this.$refs.slider.getIndex() + 1)
        this.interval = setInterval(() => {
          if (this.$refs.slider.getIndex() === 21) {
            clearInterval(this.interval)
            return
          }
          this.$refs.slider.setIndex(this.$refs.slider.getIndex() + 1)
        }, 2000)
      } else if (this.interval) {
        this.playPressed = false
        clearInterval(this.interval)
      }
    }
  },
  components: {
    'v-map': Vue2Leaflet.Map,
    'v-geojson': Vue2Leaflet.GeoJSON,
    Loader,
    vueSlider
  },
}
</script>
<style scoped>
.leaflet-pane {
  z-index: 1;
}
.learn_notification {
  position: absolute;
  right: 50px;
  top: 50px;
  width: 300px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: #404040;
  background-color: #9BFFE6;
  border-bottom: 5px solid rgba(0, 0, 0, 0.9);
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
  z-index: 900;
}
@media screen and (max-width: 400px) {
  .learn_notification {
    right: 0;
    top: 0;
    width: 100%;
  }
}
select {
  width: 150px;
  z-index: 800;
  margin-top: 10px;
  padding: 5px 10px;
  border: 1px solid #2c3e50;
  outline: 0;
  border-radius: 5px;
  font: 15px 'Montserrat';
  transition: all 0.3s;
}
select:focus {
  box-shadow: 0 0 5px #2c3e50;
}
.slider {
  position: fixed;
  z-index: 800;
  bottom: 20px;
  width: 100%;
  display: flex;
}
.btn-history {
  position: relative;
  top: -5px;
  left: 10px;
  cursor: pointer;
  transition: transform 0.2s;
}
.btn-history:hover {
  transform: scale(1.1);
}
</style>


