<template>
  <div class="modal-outer">
    <div class="modal-backdrop"></div>
    <div class="modal">
      <div class="modal-content start">
        <div class="status">
          You have saved game.<br>Do you want to complete?
        </div>
        <div class="capitals">
          <button class="btn" @click="getSaved">Complete</button>
          <button class="btn" @click="deleteStorage">New</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    getSaved: {
      type: Function
    }
  },
  methods: {
    deleteStorage () {
      localStorage.removeItem('guessed')
      this.getSaved()
    }
  }
}
</script>
<style scoped>
body {
  overflow: hidden;
}
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  outline: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: 400px) {
  .modal {
    display: block;
  }
}
.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  opacity: 0.5;
  background-color: #000;
}
.modal-content {
  width: 100%;
  max-width: 500px;
  transform: perspective(300px);
  padding-bottom: 30px;
  background: #fff;
  font-size: 24px;
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
}
@media screen and (max-width: 400px) {
  .modal {
    margin-top: 100px;
    overflow-y: scroll;
  }
}
.modal-content::before {
  background: linear-gradient(to right, #9BFFE6 0%, #A3CCFF 100%);
  content: "";
  display: block;
  height: 5px;
  width: 100%;
}
.start {
  -webkit-animation: tiltup 0.7s;
  -moz-animation: tiltup 0.7s;
  -ms-animation: tiltup 0.7s;
  animation: tiltup 0.7s;
}
.end {
  -webkit-animation: tiltbottom 0.7s 1.5s;
  -moz-animation: tiltbottom 0.7s 1.5s;
  -ms-animation: tiltbottom 0.7s 1.5s;
  animation: tiltbottom 0.7s 1.5s;
}
.status {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #e5e5e5;
}
.status img {
  margin-right: 10px;
}
.capitals {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.capitals > button {
  margin-bottom: 10px;
  text-overflow: ellipsis;
}
.right {
  -webkit-animation: rightAnswer 2s ease;
  -moz-animation: rightAnswer 2s ease;
  -ms-animation: rightAnswer 2s ease;
  animation: rightAnswer 2s ease;
}
.wrong {
  -webkit-animation: wrongAnswer 2s ease;
  -moz-animation: wrongAnswer 2s ease;
  -ms-animation: wrongAnswer 2s ease;
  animation: wrongAnswer 2s ease;
}
@keyframes tiltup {
  0% { 
    bottom: -400px; 
    opacity: 0; 
    transform: rotateX(-90deg);
    border-top: solid 40px; 
  } 
  30% {
    opacity: 0;
  }
  100% {
    bottom: 0;
  }
}
@keyframes tiltbottom {
  0% {
    bottom: 0;
  }
  100% {
    bottom: -400px; 
    opacity: 0; 
    transform: rotateX(-90deg);
    border-top: solid 40px; 
  }
}
@keyframes rightAnswer {
  0% {
    background: #91DC5A;
    --box-shadow-color: #3a5824;
  }
  25% {
    background: #9BFFE6;
    --box-shadow-color: #6cb2a1;
  }
  50% {
    background: #91DC5A;
    --box-shadow-color: #3a5824;
  }
  75% {
    background: #9BFFE6;
    --box-shadow-color: #6cb2a1;
  }
  100% { 
    background: #91DC5A;
    --box-shadow-color: #3a5824;
  }
}
@keyframes wrongAnswer {
  0% {
    background: tomato;
    --box-shadow-color: #66271c;
  }
  25% {
    background: #9BFFE6;
    --box-shadow-color: #6cb2a1;
  }
  50% {
    background: tomato;
    --box-shadow-color: #66271c;
  }
  75% {
    background: #9BFFE6;
    --box-shadow-color: #6cb2a1;
  }
  100% { 
    background: tomato;
    --box-shadow-color: #66271c;
  }
}
</style>


